.registration-form {
	color: $darc_shade_50;
  @import '../../form_elements/__border/_border';
  @import '../../form_elements/__text-field/__text-field';

  &__inputs {
    text-transform: uppercase;
  }

  @import '../../form_elements/__gender/__gender';

  &__title {
    Margin-bottom: 20px;
  }

  &__btn {
    position: relative;
    @extend %btn_size_1;
    @extend %btn_colored;
    margin-bottom: 40px;
  }

  &__login {
		margin-bottom: 3px;
	}
}

.registration-input {
  &__title {
    margin-top: 5px;
    margin-bottom: 5px;
  }

  &__email {
    margin-top: 10px;
  }

  &__special-offer {
    @import '../../form_elements/__special-offer/_special-offer';
    margin-bottom: 18px;
  }

  &__offer-text {
    display: inline;
    position: relative;
    left: 50px;
    bottom: 2px;
  }
}

.login {
  display: flex;
  justify-content: space-between;

  &__text {
    color: #000;
  }

  &__btn {
    position: relative;
    bottom: 12px;
		left: 4px;
    @extend %btn_size_2;
    @extend %btn_white;
    width: 100px;
    height: 45px;
    cursor: pointer;
  }
}