@font-face {
  font-family: Montserrat;
  src: url('./assets/fonts/Montserrat-Regular.ttf') format('ttf'), url('./assets/fonts/Montserrat-Regular.woff') format('woff'), url('./assets/fonts/Montserrat-Regular.svg') format('svg');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Montserrat;
  src: url('./assets/fonts/Montserrat-Bold.ttf') format('ttf'), url('./assets/fonts/Montserrat-Bold.woff') format('woff'), url('./assets/fonts/Montserrat-Bold.svg') format('svg');
  font-weight: 700;
  font-style: normal;
}

.material-icons-outlined {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  position: absolute;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga';
}