html,
body {
  height: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: $darc_shade_75;
}

h1 {
  color: $darc_shade_100;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
}

h2 {
  color: $darc_shade_100;
  font-weight: 700;
  font-size: 19px;
  line-height: 23px;
}

h3 {
  color: $darc_shade_100;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 1 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  width: 1190px;
  margin: 0 auto;
  padding: 15px;
}

.rub:after {
  content: "₽";
}
